import React, { useState } from "react";
import { Input, Button, List, Avatar, message } from "antd";
import { SendOutlined } from "@ant-design/icons";
import axios from "axios";
import TypingText from "./typing";

const ChatBox = () => {
  const [history, setHistory] = useState([
    {
      type: "bot",
      text: "👋 Hey there! I'm Greg, your friendly AI sidekick at HGreg, here to supercharge our dealership's success together!",
    },
  ]);
  const [prompt, setPrompt] = useState("");
  const [isAnswered, setIsAnswered] = useState(true);

  const handleSubmit = async (e: any) => {
    e.preventDefault();
    if (!isAnswered) {
      message.warning("AI Bot is thinking! Please wait!");
      return;
    }
    setIsAnswered(false);
    history.push({ type: "human", text: prompt });
    setHistory([...history]);
    setPrompt("");

    let formData = new FormData();
    formData.append("prompt", prompt);

    const response = await axios.post(
      "https://shark-app-t3f7m.ondigitalocean.app/api/chat",
      formData
    );
    history.push({ type: "bot", text: response.data.answer });
    setHistory([...history]);
    console.log(history);
    setIsAnswered(true);
  };

  return (
    <div style={{ flex: 1 }}>
      {history.map((item, key) =>
        item.type == "human" ? (
          <div className="flex mb-2" key={key}>
            <Avatar src="/assets/images/user.png" className="mr-4" />
            <div style={{ width: "1024px" }}>
              <h1 className="mb-2">Question</h1>
              <pre className="text-sm text-gray-400">{item.text}</pre>
            </div>
          </div>
        ) : (
          <div className="flex mb-2" key={key}>
            <Avatar src="/assets/images/bot.png" className="mr-4" />
            <div style={{ width: "1024px" }}>
              <h1 className="mb-2">Answer</h1>
              <div
                className="text-sm text-gray-400"
                style={{ whiteSpace: "pre-wrap" }}
              >
                <TypingText text={item.text} />
                {/* {item.text} */}
              </div>
            </div>
          </div>
        )
      )}
      {/* <List
        className="chat_history"
        itemLayout="horizontal"
        dataSource={history}
        renderItem={(item) => (
          <List.Item>
            <List.Item.Meta
              className="chat"
              avatar={
                <Avatar
                  src={
                    item.type === "human"
                      ? "/assets/images/user.png"
                      : "/assets/images/bot.png"
                  }
                />
              }
              title={item.type === "human" ? "Question" : "Answer"}
              description={item.text}
            />
          </List.Item>
        )}
      ></List> */}
      <form className="message_box" onSubmit={handleSubmit}>
        <Input.Group compact>
          <Input
            style={{ width: "calc(100% - 50px)", textAlign: "left" }}
            onChange={(e) => setPrompt(e.target.value)}
            size="large"
            value={prompt}
          />
          <Button
            htmlType="submit"
            type="primary"
            shape="circle"
            icon={<SendOutlined />}
            size="large"
          />
        </Input.Group>
      </form>
    </div>
  );
};

export default ChatBox;
