import { createAsyncThunk } from "@reduxjs/toolkit";
import api from "../../utils/api";

export const loginGoogle = createAsyncThunk(
  "/auth/loginGoogle",
  async (payload) => {
    const { data } = await api.post("/login", payload);
    localStorage.setItem("user", data.user);
    console.log(data);
    return data;
  }
);
