import { useState, useEffect } from "react";
import Document from "./document";
import Add from "./add";
import axios from "axios";
import { FilterFilled } from "@ant-design/icons";

const PdfList = () => {
  const [files, setFiles] = useState([]);
  const [selected, setSelected] = useState([]);
  const [search, setSearch] = useState("");
  const [data, setData] = useState([]);
  const [colors, setColors] = useState([{}]);

  const [modal, setModal] = useState(false);

  const updateFiles = (u_files: any) => {
    setFiles(u_files);
    setData(u_files);
    let currentColors = colors;
    for (let file of u_files) {
      let tags = file.tags.replaceAll(" ", "").split(",");
      for (let tag of tags) {
        let isContain: Number = currentColors.filter(
          (item: any) => item.tag === tag
        ).length;
        if (!isContain)
          currentColors.push({
            tag: tag,
            r: Math.round(Math.random() * 255),
            g: Math.round(Math.random() * 255),
            b: Math.round(Math.random() * 255),
          });
      }
    }
    setColors(currentColors);
  };

  const deleteDocs = async () => {
    if (window.confirm("Are you really want to remove this file?")) {
      let formData = new FormData();
      formData.append("file", selected.join(","));
      const response = await axios.post(
        "http://localhost:5000/api/removeFile",
        formData
      );
      setFiles(response.data.files);
      setData(response.data.files);
    }
  };

  const getSearch = (e: any) => {
    setSearch(e);
    setData(
      files.filter(
        (file: any, key: any) =>
          file.name.search(e) >= 0 ||
          file.desc.search(e) >= 0 ||
          file.tags.search(e) >= 0
      )
    );
  };

  useEffect(() => {
    if (localStorage.getItem("credential") == null) {
      console.log(111);
      window.location.replace("https://lobster-app-7xwbx.ondigitalocean.app/");
    }
  }, []);

  return (
    <div className="px-6 py-8 mx-auto max-w-screen-2xl xl:px-12">
      <div className="w-full flex justify-center">
        <button
          className="flex items-center px-4 py-3 space-x-1 text-sm font-semibold leading-4 transition-colors duration-300 bg-blue-600 rounded-md hover:bg-blue-700 text-blue-50"
          onClick={() => setModal(true)}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="w-4 h-4 "
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="3"
              d="M12 6v6m0 0v6m0-6h6m-6 0H6"
            ></path>
          </svg>
          <span>New Document</span>
        </button>
      </div>
      {modal === true ? <Add setModal={setModal} /> : null}
    </div>
  );
};

export default PdfList;
